<template>
    <v-container class="employee-container" pa-0>
        <sidebar />

        <v-toolbar class="toolbar">
            <v-toolbar-title class="toolbar-title">
                <v-toolbar-side-icon @click="toggleSidebar" color="primary"></v-toolbar-side-icon>
                <span class="titleText">Workplace Facility</span>
            </v-toolbar-title>
            <template>
                <v-autocomplete
                    v-model="searchedItemId"
                    :items="items"
                    :loading="isLoading"
                    :search-input.sync="searchEntities"
                    flat
                    solo
                    hide-details
                    prepend-inner-icon="search"
                    class="hidden-sm-and-down"
                    item-text="name"
                    item-value="id"
                    label="Search for assets or hazards..."
                    clearable
                >
                    <template v-slot:no-data>
                        <v-layout pl-3>Search for anything</v-layout>
                    </template>
                    <template v-slot:item="{ item }">
                        <v-list-tile-action>
                            <v-list-tile-avatar v-if="item.symbol.includes('@')">
                                <img :src="require(`../../assets/${item.icon}.jpg`)" alt="avatar" />
                            </v-list-tile-avatar>
                            <v-list-tile-avatar v-else>
                                <img :src="require(`../../assets/default_flr.png`)" alt="avatar" />
                            </v-list-tile-avatar>
                        </v-list-tile-action>
                        <v-list-tile-content>
                            <v-list-tile-title v-text="item.name"></v-list-tile-title>
                            <v-list-tile-sub-title v-text="item.symbol"></v-list-tile-sub-title>
                        </v-list-tile-content>
                    </template>
                </v-autocomplete>
            </template>
            <v-spacer></v-spacer>
            <v-btn icon>
                <v-icon color="primary">notifications</v-icon>
            </v-btn>
            <userInfo></userInfo>
        </v-toolbar>
        <router-view></router-view>
    </v-container>
</template>

<script>
import Sidebar from '@/components/sidebar/sidebar.vue'
import UserInfo from '@/components/layout/UserInfo'
import { searchService } from '../../services/search.service'

import { mapActions } from 'vuex'

export default {
    data: () => ({
        drawer: false,
        isLoading: false,
        items: [],
        searchedItemId: null,
        searchEntities: null
    }),

    watch: {
        searchEntities(val) {
            // Items have already been loaded
            if (this.items.length > 0) return

            this.isLoading = true
            searchService
                .search(val)
                .then(result => {
                    result.map(
                        (obj, index) =>
                            (obj.icon = index
                                .toString()
                                .split('')
                                .pop())
                    )
                    this.items = result
                })
                .catch(err => {
                    throw err
                })
                .finally(() => (this.isLoading = false))
        },
        searchedItemId() {
            searchService.getSearchResultLocation(this.searchedItemId).then(location => {
                if (location.deskId !== null) {
                    this.changeFloor(location.floorId)
                    this.selectAsset(location.deskId)
                } else {
                    this.removeAsset()
                }
            })
        }
    },
    components: {
        userInfo: UserInfo,
        sidebar: Sidebar
    },
    methods: {
        ...mapActions('sidebar', ['toggleSidebar']),
        ...mapActions('mapInteractions', ['changeFloor', 'selectAsset', 'removeAsset'])
    }
}
</script>

<style lang="scss" scoped>
@import 'src/stylesheets/1-setttings/settings.colors.scss';

.employee-container {
    min-width: 100%;
    z-index: 2;
}

.toolbar {
    z-index: 3;
    height: 65px;
}

.titleText {
    color: $orange;
}

.toolbar-title {
    width: 300px;
}

* {
    box-sizing: border-box;
}
</style>
