<template>
    <floor-selector :class="$style.sidebar" @floorSelected="onFloorSelected"></floor-selector>
</template>

<script>
import FloorSelector from '@/components/employee/FloorSelector.vue'
import { mapState, mapActions, mapGetters } from 'vuex'

export default {
    name: 'sidebar',
    mounted() {
        this.init(this.$el)
    },
    computed: {
        ...mapState('sidebar', ['sidebarOpen']),
        ...mapGetters('sidebar', ['sidebarOpen'])
    },
    methods: {
        ...mapActions('sidebar', ['init', 'showIt']),
        ...mapActions('account', ['changeFloor']),
        onFloorSelected(floorId) {
            this.changeFloor(floorId)
        }
    },
    components: {
        floorSelector: FloorSelector
    },
    watch: {
        sidebarOpen: function(open) {
            this.showIt({ isOpen: open, element: this.$el })
        }
    }
}
</script>

<style module>
.sidebar {
    align-content: center;
    position: fixed;
    left: 0;
    margin-top: 70px;
    width: 300px;
    height: 100vh;
    max-width: 90vw;
    z-index: 20;
}
</style>
